@use "../styles.scss";

.project-card .container {
    display: flex;
    height: 300px;

    a {
        color: inherit;
        text-decoration: none;
    }

    .project-image {
        position: relative;
        background-color: black;
        overflow: hidden;
        flex: 1 1 0%;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.3));

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: left top;
            transition: 0.3s;
        }

        &:hover img {
            transform: scale(1.05);
            -ms-transform: scale(1.05); /* IE 9 */
            -moz-transform: scale(1.05); /* Firefox */
            -webkit-transform: scale(1.05); /* Safari and Chrome */
            -o-transform: scale(1.05); /* Opera */
        }
    }

    .project-detail {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 500px;
        box-sizing: content-box;

        .card-header {
            font-size: 1.25rem; //20px;
        }

        p {
            margin-bottom:1.5rem;
            max-width: 400px;
            @include styles.text;
        }

        .project-links {
            text-transform: uppercase;
            font-weight: 700;
            font-size: 0.9375rem; //15px;

            .project-link {
                margin-right: 1.5rem;
                color: styles.$lightBlue;

                &:hover {
                    border-bottom: 2px solid styles.$lightBlue;
                }
            }

            .project-source {
                color: styles.$purple;

                &:hover {
                    border-bottom: 2px solid styles.$purple;
                }
            }

            a {
                border-bottom: 2px solid transparent;
                transition: 0.3s;
            }
        }
    }

    @media (max-width: styles.$bp-1) {
        .project-detail {
            width: 350px;

            .card-header {
                margin-right: 12px;
            }
            p {
                margin-right: 12px;
            }
        }
    }

    @media (max-width: styles.$bp-3) {
        .project-detail {
            width: 280px;

            .card-header {
                font-size: 1.125rem; //18px;
            }
        }
    }

    @media (max-width: styles.$bp-4) {
        flex-direction: column;
        height: auto;

        .project-detail {
            width: auto;
            order: 2;
            align-items: center;

            .card-header {
                font-size: 1rem; //16px;
            }

            p {
                padding: 0 0.75rem;
                max-width: 400px;
                text-align: center;
                margin-right: 0;
                margin-bottom: 1rem;
            }

            .project-links {
                font-size: 0.875rem; //14px
            }
        }

        .project-image {
            order: 1;
            margin-bottom: 0.75rem;
            height: 200px;
            flex: auto;
        }
    }

    @media (max-width: styles.$bp-mobile) {
        .project-detail {
            .card-header {
                font-size: 0.9375rem; //15px;
            }

            .project-links {
                font-size: 0.8125rem; //13px
            }
        }
    }
}
