@use "../../components/styles.scss";

.about {
  .about-container {
    display: flex;
    padding: 4rem 0; //64px

    @media (max-width: styles.$bp-3) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 2rem;
    }
  }

  .about-image {
    flex: 1 1 0%;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      display: block;
      width: 15rem; //240px
      transition: 0.3s;

      &:hover {
        transform: scale(1.05);
        -ms-transform: scale(1.05); /* IE 9 */
        -moz-transform: scale(1.05); /* Firefox */
        -webkit-transform: scale(1.05); /* Safari and Chrome */
        -o-transform: scale(1.05); /* Opera */
      }
    }
  }

  .about-content {
    flex: 1 1 0%;

    h2 {
      font-weight: 700;
      font-size: 1.25rem; //20px
      margin: 0 0 0.75rem 0;

      @media (max-width: styles.$bp-3) {
        text-align: center;
      }

      @media (max-width: styles.$bp-4) {
        font-size: 1rem; //16px
      }

      @media (max-width: styles.$bp-mobile) {
        font-size: 0.9375rem; //15px
      }
    }

    p {
      @include styles.text;
      max-width: 27rem; //432px;
      white-space: pre-wrap;
      br {
        display: block;
      }
    }
  }
}
