@use "../styles.scss";

.header {
  transition: 0.3s;
}

.header .header-nav {
  text-align: right;
  padding: 1rem 0;

  a {
    text-decoration: none;
    color: inherit;
  }

  .nav-link {
    display: inline-block;
    position: relative;
    padding: 1rem;
    text-align: center;

    span {
      @include styles.link;
      display: inline-block;
      position: relative;
      bottom: 0;
      transition: all 0.2s, color 0s;
    }

    &:hover > span {
      @include styles.linkActive;
      bottom: 3px;
    }

    &::after {
      position: absolute;
      content: "";
      width: 75%;
      height: 2px;
      background: transparent;
      bottom: 0.5rem;
      left: 0;
      right: 0;
      margin: 0 auto;
      transition: 0.2s;
    }

    &:hover::after {
      background: styles.$dark;
    }

    .p {
      width: 5.25rem; //84px

      @media (max-width: styles.$bp-4) {
        width: 4.75rem; //76px
      }

      @media (max-width: styles.$bp-mobile) {
        width: 4.25rem; //68px
      }
    }

    .a {
      width: 3.5rem; //56px;

      @media (max-width: styles.$bp-4) {
        width: 3.25rem; //52px
      }

      @media (max-width: styles.$bp-mobile) {
        width: 3rem; //48px
      }
    }

    .c {
      width: 5rem; //80px;

      @media (max-width: styles.$bp-4) {
        width: 4.5rem; //72px
      }

      @media (max-width: styles.$bp-mobile) {
        width: 4.25rem; //68px
      }
    }
  }

  .nav-link-active {
    span {
      @include styles.linkActive;
    }

    &::after {
      background: styles.$dark;
    }
  }

  @media (max-width: styles.$bp-4) {
    text-align: center;

    .nav-link {
      padding: 1rem 0.5rem;
    }
  }
}
