@use "../styles.scss";

.hero {
  padding: 4rem 0;
  transition: 0.3s;

  .hero-container {
    display: flex;
    justify-content: center;
  }

  .hero-wrap {
    display: flex;
  }

  .hero-text {
    display: inline-block;

    h1 {
      font-size: 4rem; //64px;
      line-height: 5rem; //80px;

      span {
        color: styles.$blue;
      }
    }

    p {
      @include styles.text;
      padding-top: 1rem;
      max-width: 28rem;
    }
  }

  .hero-img {
    display: inline-block;
    
    img {
      margin-top: 12px;
      margin-right: 64px;
      width: 100px;
      border-radius: 50%;
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.3));
    }
  }

  @media (max-width: styles.$bp-2) {
    .hero-text {
      h1 {
        font-size: 3rem; //48px;
        line-height: 3.5rem; //56px;
      }

      p {
        max-width: 25rem;
      }
    }

    .hero-img {
      img {
        margin-right: 36px;
      }
    }
  }

  @media (max-width: styles.$bp-4) {
    .hero-wrap {
      flex-direction: column;
    }

    .hero-img {
      text-align: center;

      img {
        width: 80px;
        margin: 0 0 12px 0;
      }
    }

    .hero-text {
      text-align: center;
      h1 {
        font-size: 2.25rem; //36px;
        line-height: 2.5rem; //40px;
      }

      p {
        max-width: 340px;
      }
    }
  }

  @media (max-width: styles.$bp-mobile) {
    .hero-text {
      h1 {
        font-size: 1.875rem; //30px;
        line-height: 2.25rem; //36px;
      }

      p {
        max-width: 300px;
      }
    }
  }
}
