//Breakpoints
$bp-0: 1440px;
$bp-1: 1100px;
$bp-2: 992px;
$bp-3: 768px;
$bp-4: 576px;
$bp-mobile: 375px;

//Colors
$bg-color: #ffffff;
$dark: #171717;
$blue: #0284c7;
$lightBlue: #0ea5e9;
$purple: #8b5cf6;

@mixin link {
    font-size: 1rem; //16px
    text-transform: uppercase;
    font-weight: 500;
    opacity: 0.6;

    @media (max-width: $bp-4) {
        font-size: 0.875rem; //14px
    }

    @media (max-width: $bp-mobile) {
        font-size: 0.8125rem; //13px
    }
}

@mixin linkActive {
    font-weight: 700;
    opacity: 1;
}

@mixin text {
    font-size: 0.9375rem; //15px;
    opacity: 0.6;

    @media (max-width: $bp-4) {
        font-size: 0.875rem; //14px
    }

    @media (max-width: $bp-mobile) {
        font-size: 0.8125rem; //13px
    }
}

@mixin pageHeader {
    font-size: 2.5rem; //40px
    text-transform: uppercase;

    @media (max-width: $bp-2) {
        font-size: 1.875rem; //30px;
    }

    @media (max-width: $bp-4) {
        font-size: 1.5rem; //24px;
    }

    @media (max-width: $bp-mobile) {
        font-size: 1.25rem; //20px;
    }
}
