.loading {
    height: 100vh;
    min-height: 140px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    img {
        width: 64px;
    }

    svg {
        display: block;
    }

    .loading-icon {
        width: 48px;
        height: 48px;
    }

    span {
        font-size: 0.875rem;
    }
}
