@use "../styles.scss";

.footer {
  text-align: center;
  transition: 0.3s;

  .main-container {
    padding-bottom: 3rem;

    hr {
      margin: 0;
      border: none;
      height: 1px;
      opacity: 0.6;
      color: styles.$dark; /* old IE */
      background-color: styles.$dark; /* Modern Browsers */
      margin-bottom: 3rem;
    }

    .footer-header {
      font-size: 1.25rem;
      font-weight: 700;
      text-transform: uppercase;

      @media (max-width: styles.$bp-4) {
        font-size: 0.9375rem; //15px
      }
    }
  }

  #copyright {
    font-size: 0.75rem;
  }
}

.footer .footer-nav {
  padding: 1rem 0;

  a {
    text-decoration: none;
    color: inherit;
  }

  .social {
    display: inline-block;
    position: relative;
    padding: 1rem;

    span {
      @include styles.link;
      display: inline-block;
      position: relative;
      bottom: 0;
      transition: 0.2s;
    }

    &:hover span {
      @include styles.linkActive;
      bottom: 3px;
    }

    &::after {
      position: absolute;
      content: "";
      width: 75%;
      height: 2px;
      background: transparent;
      bottom: 0.5rem;
      left: 0;
      right: 0;
      margin: 0 auto;
      transition: 0.2s;
    }

    &:hover::after {
      background: styles.$dark;
    }

    .e {
      width: 3.25rem; //52px

      @media (max-width: styles.$bp-4) {
        width: 2.75rem; //44px
      }

      @media (max-width: styles.$bp-mobile) {
        width: 2.5rem; //40px
      }
    }

    .g {
      width: 4rem; //64px

      @media (max-width: styles.$bp-4) {
        width: 3.5rem; //56px
      }

      @media (max-width: styles.$bp-mobile) {
        width: 3.25rem; //52px
      }
    }

    .l {
      width: 4.75rem; //76px;

      @media (max-width: styles.$bp-4) {
        width: 4rem; //64px
      }

      @media (max-width: styles.$bp-mobile) {
        width: 3.75rem; //60px
      }
    }
  }
}
