@use "../styles.scss";

.app {
    display: flex;
    flex-direction: column;
    min-width: 375px;

    .main-container {
        max-width: 75rem; //1200px
        margin: auto;
        padding: 0 3rem;

        @media (max-width: styles.$bp-3) {
            padding: 0 2rem;
        }

        @media (max-width: styles.$bp-mobile) {
            padding: 0 1.5rem;
        }
    }

    main {
        flex-grow: 1;

        .main-container {
            margin: 0 auto;
        }
    }

    .main-header {
        @include styles.pageHeader;
        display: block;

        &::after {
            display: block;
            background-color: styles.$dark;
            content: "";
            height: 1px;
            width: 100%;
            margin-top: 12px;
            opacity: 0.6;
        }

        @media (max-width: styles.$bp-4) {
            text-align: center;
        }
    }
}

.app-enter {
    opacity: 0;
}
.app-exit {
    opacity: 1;
}
.app-enter-active {
    opacity: 1;
}
.app-exit-active {
    opacity: 0;
}
.app-enter-active,
.app-exit-active {
    transition: opacity 200ms;
}
